.reviews {
  padding-top: 80px;
  @include desktop-small {
    padding-top: 156px;
  }
  & .container {
    overflow: hidden;
  }
}
.reviews-title {
  @include text($font-family: 'Cormorant', $a: 700, $b: 25px, $c: 30px);
  text-transform: uppercase;
  color: var(--text);
  margin-bottom: 40px;
  @include tablet {
    @include text($font-family: 'Cormorant', $a: 700, $b: 60px, $c: 55px);
    margin-bottom: 80px;
  }
}
.reviews-swiper {
  @include desktop-small {
    padding: 0 100px;
  }
  @include desktop {
    padding: 0;
  }
}
.reviews-swiper__wrap.swiper-wrapper {
  margin-bottom: 10px;
  @include desktop-small {
    margin-bottom: 60px;
  }
  @include desktop {
    margin-bottom: 50px;
  }
}
.reviews-slid.swiper-slide {
  transition: all var(--main-transition);

  width: 100%;
  @include desktop-small {
    display: flex;
  }
  @include desktop {
    width: 577px;
    transform: scale(0.9);
  }
  &.swiper-slide-active {
    @include desktop-small {
      transform: scale(1.3);
    }
    @include desktop {
      transform: scale(1.2);
    }
  }
  @media screen and (min-width: 768px) and (max-width: 999.9px) {
    &:nth-child(2n - 1) {
      img {
        border-radius: 200px 0 0 0;
      }
    }
    &:nth-child(2n) {
      img {
        border-radius: 0 200px 0 0;
      }
    }
  }
}
.slid-text-wrap {
  margin-top: 30px;
  background-color: var(--background);
  padding: 20px;
  text-align: center;
  @include tablet {
    padding: 40px;
    text-align: left;
    margin-top: 0;
  }
  @include desktop-small {
    padding: 50px 30px;
  }
  @include desktop {
    padding: 15px;
  }
}

.review-image {
  img {
    width: 200px;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
    @include tablet {
      width: 100%;
      border-radius: 0;
    }
    @include desktop-small {
      width: 180px;
    }
  }
}
.review-name {
  @include text($font-family: 'Montserrat', $a: 700, $b: 18px, $c: 22px);
  text-transform: uppercase;
  margin-bottom: 15px;
  color: var(--text);
  @include desktop-small {
    @include text($font-family: 'Montserrat', $a: 700, $b: 25px, $c: 33px);
    margin-bottom: 10px;
  }
  @include desktop {
    margin-bottom: 10px;
  }
}
.review-type {
  @include text($font-family: 'Montserrat', $a: 700, $b: 17px, $c: 20px);
  color: rgba(83, 103, 121, 0.75);
  margin-bottom: 23px;
  @include desktop-small {
    margin-bottom: 45px;
  }
  @include desktop {
    margin-bottom: 15px;
  }
}
.reviw-text {
  @include text($font-family: 'Montserrat', $a: 400, $b: 16px, $c: 20px);
  text-align: left;
  &.first {
    margin-bottom: 15px;
  }

  color: var(--text);
  @include desktop-small {
    @include text($font-family: 'Montserrat', $a: 400, $b: 17px, $c: 20px);
  }
}
.reviews-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}
.reviews-pagination .swiper-pagination-bullet {
  background: radial-gradient(
      50% 50% at 50% 50%,
      rgba(247, 151, 129, 0.2) 0%,
      rgba(242, 218, 213, 0) 100%
    ),
    #f79781;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  &:not(:last-child) {
    margin-right: 5px;
  }
}
.reviews-pagination .swiper-pagination-bullet-active {
  width: 20px;
  height: 20px;
}
